.select-item {
  margin: 0 auto;
}
.select-item .item-grade {
  display: -ms-flexbox;
  display: flex;
  line-height: 2rem;
  overflow: hidden;
  padding-left: 1rem;
}
.select-item .item-grade .item-grade-p {
  width: 5rem;
  background: #fff;
  color: #666666;
  text-align: center;
  font-weight: bold;
  line-height: 3rem;
  font-size: 0.88rem;
  font-family: MicrosoftYaHei;
  margin: 0;
}
.select-item .item-grade .item-grade-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.select-item .item-grade .item-grade-row .item-grade-row-itme {
  width: 54rem;
}
.select-item .item-grade .item-grade-row .item-grade-row-itme .item-grade-row-itme-a {
  display: inline-block;
  text-align: center;
  width: 6rem;
  line-height: 3rem;
  font-size: 0.88rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #666666;
}
.select-item .item-grade .item-grade-row .item-grade-row-itme .item-grade-row-itme-a:hover {
  color: #40a9ff;
}
.select-item .item-grade .item-grade-row .item-grade-row-itme .item-grade-row-itme-a.item-grade-row-itme-a-selected span {
  background: #028BFF;
  color: #fff;
  display: inline-block;
  height: 1.5rem;
  line-height: 1.5rem;
  border-radius: 4px;
  padding: 0 0.5rem;
}
.select-item .item_hr {
  margin: 0;
  background: #f4f4f4;
  height: 6px;
  border: none;
}
.item-grade-right {
  width: 5rem;
  height: 2rem;
  line-height: 3rem;
  text-align: center;
  margin-right: 1rem;
  display: none;
}
.item-grade-right .item-grade-right-a {
  width: 3rem;
  height: 1.3rem;
  line-height: 1.3rem;
  display: inline-block;
  background: #ecebea;
  color: #806f66;
  border-radius: 4px;
  font-size: 0.7rem;
}
